<template>
  <div>
    <v-menu offset-y>
      <template v-slot:activator="{ on, attrs }">
        <v-avatar v-bind="attrs" v-on="on">
          <v-img
            aspect-ratio="1"
            alt="Avatar"
            :src="
              $auth.user.avatar
                ? 'http://68.183.159.113/uploads/users/' + $auth.user.avatar
                : '/potrait.jpg'
            "
          >
            <template v-slot:placeholder>
              <v-row class="fill-height ma-0" align="center" justify="center">
                <v-progress-circular
                  indeterminate
                  color="primary"
                ></v-progress-circular>
              </v-row>
            </template>
          </v-img>
        </v-avatar>
      </template>
      <v-list>
        <v-list-item
          v-for="(item, index) in items"
          :key="index"
          :link="item.link"
          exact
          :to="item.link ? $i18n.path(item.route) : ''"
          color="primary"
        >
          <!-- <v-list-item-action> -->
          <v-icon class="mr-2">{{ item.icon }}</v-icon>
          <!-- </v-list-item-action> -->
          <v-list-item-title>{{ item.title }}</v-list-item-title>
        </v-list-item>
        <v-list-item>
          <v-icon class="mr-2">mdi-logout</v-icon>
          <v-list-item-title class="cursor-pointer" @click="logout">
            {{ $t('dashboard.accountDropDown.links.logout') }}
          </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [
        {
          title: this.$t('dashboard.accountDropDown.links.home'),
          link: true,
          route: '/',
          icon: 'mdi-home'
        },
        {
          title: this.$t('dashboard.accountDropDown.links.dashboard'),
          link: true,
          route: '/dashboard',
          icon: 'mdi-cog'
        },
        {
          title: this.$t('dashboard.accountDropDown.links.profile'),
          link: true,
          route: '/dashboard/profile/' + this.$auth.user._id,
          icon: 'mdi-account'
        }
      ]
    }
  },
  methods: {
    async logout() {
      await this.$auth.logout()
      this.$router.push('/')
    }
  }
}
</script>

<style lang="scss" scoped>
.cursor-pointer {
  cursor: pointer;
}
</style>
